
#nav{
	width: 100%;
	flex: 0 0 3.5rem;
	box-sizing: border-box;
	background-color: #333;
	height: 3.5rem;
	display: flex;
	flex-wrap: nowrap;
	z-index: 2;
}
#nav:before{
	content: '';
	width: 4rem;
	height: 4rem;
	border-radius: 100%;
	background-color: #333;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform:translateX(-50%);
}
#nav>a{
	flex-basis: 100%;
	text-decoration: none;
	justify-content: center;
	align-items: center;
	display: flex;
	color:#FFF;
	flex-direction: column;
	padding: 6px 3px;
	position: relative;
}
#nav>a>span{
	font-size: 0.5rem;
	line-height: 1;
	margin-top: 5px;
	flex: 1;
}
#nav>a>.svg-icon{
	flex: 2;
}



