
#head{
	width: 100%;
	flex: 0 0 3rem;
	background-color: #1A1A1A;
	display: flex;
	justify-content: center;
	align-items: center;
	color:#FFF;
  	z-index: 2;
}
#head>a{
	text-decoration: none;
	color:#FFF;
}
#menuBtn{
	position: absolute;
	left: 1rem;
	top: 1rem;
	width: 1rem;
}
#menuBtn>.svg-icon{
	padding: 50% 0;
}



