
#tutorial{
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: #FFF;
		top: 0;
		left: 0;
		overflow:hidden;
    z-index: 99;
}
.steps{
		height: 100%;
		white-space: nowrap;
		position: relative;
		transition: left 300ms;
		font-size: 0;
}
.stepMove{
		transition: left 0ms;
}
.steps[data-step='0']{
		left: 0;
}
.steps[data-step='1']{
		left: -100%;
}
.steps[data-step='2']{
		left: -200%;
}
.steps[data-step='3']{
		left: -300%;
}
.step{
		width: 100%;
		height: 100%;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-size: 1rem;
		background-size: contain;
		background-position: center bottom;
		background-repeat: no-repeat;
}
.text{
		position: absolute;
		top:6rem;
		font-weight: bold;
		font-size: 1.2rem;
}
#step-nav{
		position: absolute;
		width: 100%;
		top:3rem;
		display: flex;
		justify-content: center;
		font-size: 0;
}
#step-nav span{
		width:0.5rem;
		height:0.5rem;
		border-radius: 100%;
		background-color: #FFF;
		border:1px solid #000;
		display: inline-block;
		margin: 0 0.4rem
}
#step-nav span.act{
		background-color: #000;
}
.start-btn{
		position: absolute;
		top: 1.5rem;
		right: 1.5rem;
		font-size: 1rem;
		color:#999;
}



