
body{
	margin: 0;
	min-height: 100%;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	height: 100%;
	background-color: #2b2b2b;
	max-width: 414px;
	height: 100vh;
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	position: relative;
}
#main {
	overflow-x: hidden;
	overflow-y: auto;
	flex: 1 1 auto;
}
.mainView{
	width: 100%;
	box-sizing: border-box;
}
.svg-icon{
	width: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	height: 0;
	width: 100%;
}
.svg-icon>img{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.mask{
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.5);
	position: fixed;
	left: 0;
	top: 0;
	opacity: 100;
	z-index: 3;
}
.mask-enter,.mask-leave-active{
	opacity: 0
}
.mask-enter-active,.mask-leave-active{
	transition: opacity 300ms ease;
}



